export default {
	props:{
		resdata:[Array,Object],
		height:{
			type:String,
			default:"350"
		},
		preview:{
			type:Boolean,
			default:false
		}
	},
	computed: {
		getStyle() {
			return `height: ${this.height}rpx;`
		},
		getUrls(){
			var urls = []
			for (let v in this.resdata) {
				urls.push(this.resdata[v].src)
			}
			return urls
		}
	},
	methods:{
		event(item,index){
			if (this.preview) {
				return uni.previewImage({
					current:index,
					urls:this.getUrls,
					indicator:"default"
				})
			}
		}
	}
}
